  .checkbox input {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    white-space: nowrap;
    border: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(100%); }
  .checkbox input:checked + label::after {
    background: url("../img/svg/icon-checkbox.svg") center center no-repeat; }
  .checkbox input:focus + label::before {
    background-color: #ffffff; }
  .checkbox label {
    padding-left: 26px;
    position: relative;
